import { mapGetters } from "vuex";
import { queryCarChargingChart } from "api/sysVehicle";
import { getTimeStampfunction } from "utils/validate";
import line from "./line";
import { formatSeconds } from "utils/validate";
export default {
  props: ["data"],
  data() {
    return {
      retAngle: {
        xList: ["00:00"],
        yList: [0]
      },
      retPowerAmpere: {
        xList: ["00:00"],
        yList: [0]
      },
      retTemperature: {
        xList: ["00:00"],
        yList: [0]
      },
      retPowerVbv: {
        xList: ["00:00"],
        yList: [0]
      },
      chargTime: "00:00",
      beginDate: "",
      endDate: ""
    };
  },
  watch: {
    data(val) {
      if (val.chargingStatus == 1 && val.stopStatus == 0) {
        this.queryCarChargingChart();
        if (val.lastChargingStopTime) {
          let time = val.lastChargingStopTime - val.lastChargingStartTime;
          this.chargTime = formatSeconds(time,1);
          return;
        } else {
          let time = new Date().getTime() - val.lastChargingStartTime;
          this.chargTime = formatSeconds(time,1);
          return;
        }
        this.chargTime = "00:00";
        return;
      }
      this.retAngle = {
        xList: ["00:00"],
        yList: [0]
      };
      this.retPowerAmpere = {
        xList: ["00:00"],
        yList: [0]
      };
      this.retTemperature = {
        xList: ["00:00"],
        yList: [0]
      };
      this.retPowerVbv = {
        xList: ["00:00"],
        yList: [0]
      };
    }
  },
  mounted() {
    setTimeout(() => {
      this.chartData = {
        xList: ["12:00", "1:00"],
        yList: [0, 0],
        min: 10,
        max: 23
      };
    }, 1000);
  },
  components: {
    "curve-line": line
  },
  methods: {
    queryCarChargingChart() {
      let data = {
        carId: this.carMsg.id,
        beginDate: getTimeStampfunction(this.data.lastChargingStartTime),
        endDate: getTimeStampfunction(this.data.lastChargingStopTime)
      };
      queryCarChargingChart(data)
        .then(res => {
          if (res.data && res.data.retX) {
            this.retTemperature = {
              xList: res.data.retX,
              yList: res.data.retTemperature
            };
            this.retAngle = {
              xList: res.data.retX,
              yList: res.data.retAngle
            };
            this.retPowerAmpere = {
              xList: res.data.retX,
              yList: res.data.retPowerAmpere
            };
            this.retPowerVbv = {
              xList: res.data.retX,
              yList: res.data.retPowerVbv
            };
            return;
          }
          this.reset();
        })
        .catch(err => {
          this.reset();
        });
    },
    reset() {
      this.retTemperature = {
        xList: ["00:00"],
        yList: [0]
      };
      this.retAngle = {
        xList: ["00:00"],
        yList: [0]
      };
      this.retPowerAmpere = {
        xList: ["00:00"],
        yList: [0]
      };
      this.retPowerVbv = {
        xList: ["00:00"],
        yList: [0]
      };
    }
  },
  computed: {
    ...mapGetters(["carMsg"])
  }
};



