
import { mapGetters } from "vuex";
import loaddata from "./loaddata";
import amap from "./map";
import tjSpinner from "components/loading/tjSpinner.vue";
import { appqueryMonitorCarStatusList } from "api/sysVehicle";
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { plusready, plusOpen } from "common/plus";
import { formatSeconds } from "utils/validate";
import { setTimeout } from "timers";
export default {
props: ["getBarHeight"],
name: "shop",
directives: {
TransferDom
},
data() {
return {
defalIndex: 0,
map: null,
cw: null,
tjLoad: false,
show: false,
carData: {},
cycl: "00:00"
};
},
watch: {
carData(val) {
if (val.lastStopTime) {
let time = val.lastStopTime - val.lastStartTime;
this.cycl = formatSeconds(time,1);
} else {
if (val.lastStartTime) {
let time = new Date().getTime() - val.lastStartTime;
this.cycl = formatSeconds(time,1);
}else{
this.cycl = "00:00"
}
}
},
carMsg(val){
this.setTime();
}





},
created() {
plusready(this.plusready);
},
mounted() {
if (process.env.NODE_ENV === "production") return;
setTimeout(() => {
this.route();
}, 500);
},
methods: {
plusready() {
this.cw = plus.webview.currentWebview();
setTimeout(() => {
this.route();
}, 500);
},
router() {
this.$router.push({ name: "apply" });
this.$emit("simClick", 2);
},
mapListen() {

this.show = true;
},
appqueryMonitorCarStatusList() {
return new Promise((reslove, reject) => {
appqueryMonitorCarStatusList(this.carMsg.id).then(res => {
if (res.data) {
this.carData = res.data;
reslove(res.data);
return;
}
this.carData = {}
});
});
},
setTime() {
this.appqueryMonitorCarStatusList().then(res => {
setTimeout(() => {
this.setTime();
}, 5000);
});
},
route() {

if (this.carMsg && this.carMsg.id != -1) {
this.setTime();
return;
}
const _this = this;
this.$vux.confirm.show({
title: "提示",
content: "请先选择一辆车",
onConfirm() {
_this.$router.push({ name: "my" });
_this.$emit("simClick", 3);
}
});
}
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
Popup,
XButton,
amap,
loaddata,
tjSpinner
},
computed: {
...mapGetters(["carMsg"]),
top() {
return parseInt(this.getBarHeight) + 47 + "px";
}
}
};



