import { plusready, plusOpen } from "common/plus";
import { fail } from "assert";
export default {
  props: ["tjLoad", "data", "cycl"],
  data() {
    let self = this;
    return {
      // amapManager,
      zoom: 15,
      center: [120.21287131, 30.20979508],
      isFirst: true,
      events: {
        init: o => {
          self.$emit("loadEnd");
          o.getCity(result => {
            // console.log(result);
          });
        }
      },
      polyline: {
        path: [],
        strokeColor: "#0EB273",
        strokeWeight: 4,
        events: {
          end: e => {
            let newPath = e.target
              .getPath()
              .map(point => [point.lng, point.lat]);
          }
        },
        editable: false
      },
      plugin: [
        {
          pName: "MapType",
          defaultType: 0,
          events: {
            init(instance) {}
          }
        },
        {
          pName: "Geolocation",
          events: {
            init(o) {
              // o 是高德地图定位插件实例
              o.getCurrentPosition((status, result) => {
                if (result && result.position) {
                  self.center = [result.position.lng,result.position.lat];
                  self.marker.position = self.center ;
                  self.$nextTick();
                }
              });
            }
          }
        }
      ],
      window: {
        position: [120.21287131, 30.20979508]
      },
      visible: false,
      marker: {
        position: [120.21287131, 30.20979508],
        offset: [-35, -72],
        events: {
          click: () => {
            self.$emit("mapListen");
            // self.$nextTick(() => {
            //   self.visible = true;
            // });
          }
        },
        icon: require("assets/img/carsWhite.png")
      }
    };
  },
  watch: {
    data(val) {
      this.getStop(val);
    }
  },
  methods: {
    getStop(val) {
      this.visible = true;
      if (val.chargingStatus == 1) {
        //充电中
        this.marker.icon = require("assets/img/carsGreen.png");
      } else if (val.alarmStatus == 0) {
        //报警
        this.marker.icon = require("assets/img/cars.png");
      } else if (val.alarmStatus == 2) {
        //预警
        this.marker.icon = require("assets/img/carsYellow.png");
      } else {
        this.marker.icon = require("assets/img/carsWhite.png");
      }
      if (
        !val.longitude ||
        val.longitude == 0 ||
        !val.latitude ||
        val.latitude == 0
      )
        return;
      let arr = [val.longitude, val.latitude];
      this.center = arr;
      this.marker.position = arr;
      this.window = {
        position: [val.longitude - 0.0005, val.latitude + 0.0028]
      };
      console.log(val);

      if (val.stopStatus == 1) {
        this.polyline.path.push(arr);
      }
    }
  }
};



